    .portrait {
        background-image: url('./images/BasicSmile.jpg');
        background-size: cover;
        background-position: top;
        position: fixed;
        left: 0;
        top: 0;
        width: 600px;
        height:100vh;
        
        padding:0;
        margin-left: 50px;
        margin-top: 50px;
        
    .basicSmile{
        background-image: url('./images/BasicSmilePng.jpg');
        background-size: cover;
        width: 25px;
        height: 25px;
        }
    
    .expressionless{
        background-image: url('./images/ExpressionlessPng.jpg');
        background-size: cover;
        width: 25px;
        height: 25px;
        }

    .surprising{
        background-image: url('./images/SurprisingPng.jpg');
        background-size: cover;
        width: 25px;
        height: 25px;
        }    
        
    }


